import React, { memo, useRef } from 'react';
import { Handle, Position, NodeProps } from 'reactflow';
import useStore from '@/store/useStore';
import { ImageNodeData } from '@/types/workflow';
import { Upload, X, Image as ImageIcon } from 'lucide-react';
import clsx from 'clsx';

const ImageNode = ({ id, data, selected }: NodeProps<ImageNodeData>) => {
  const updateNodeData = useStore((state) => state.updateNodeData);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (evt) => {
        updateNodeData(id, { imageData: evt.target?.result as string, fileName: file.name });
      };
      reader.readAsDataURL(file);
    }
  };

  const clearImage = (e: React.MouseEvent) => {
     e.stopPropagation();
     updateNodeData(id, { imageData: undefined, fileName: undefined });
  };

  return (
    <div className={clsx(
        "shadow-xl rounded-2xl bg-white dark:bg-[#0f172a] min-w-[280px] transition-all duration-300",
        selected ? 'ring-2 ring-pink-500 border-transparent' : 'border border-slate-200 dark:border-slate-800'
    )}>
        <div className="px-4 py-3 border-b border-slate-100 dark:border-slate-800 flex items-center justify-between">
            <div className="flex items-center gap-2">
                <div className="p-1.5 rounded-lg bg-pink-500/10 text-pink-500">
                    <ImageIcon size={14} className="fill-current" />
                </div>
                <span className="font-semibold text-slate-800 dark:text-slate-100 text-sm">Image Source</span>
            </div>
        </div>
        
        <div className="p-3">
            {data.imageData ? (
                <div className="relative w-full h-40 rounded-xl overflow-hidden border border-slate-200 dark:border-slate-800 group bg-slate-900/50">
                    {/* eslint-disable-next-line @next/next/no-img-element */}
                    <img src={data.imageData} alt="preview" className="w-full h-full object-contain" />
                    <button 
                        onClick={clearImage}
                        className="absolute top-2 right-2 bg-black/60 hover:bg-red-500 text-white p-1.5 rounded-full backdrop-blur-sm transition-all"
                    >
                        <X size={12} />
                    </button>
                    <div className="absolute bottom-0 inset-x-0 bg-gradient-to-t from-black/80 to-transparent p-2">
                        <p className="text-[10px] text-white/80 truncate">{data.fileName}</p>
                    </div>
                </div>
            ) : (
                <div 
                    className="w-full h-40 rounded-xl border-2 border-dashed border-slate-200 dark:border-slate-800 flex flex-col items-center justify-center cursor-pointer hover:border-pink-500 hover:bg-pink-50 dark:hover:bg-pink-500/5 transition-colors gap-2 group"
                    onClick={() => fileInputRef.current?.click()}
                >
                    <div className="p-3 rounded-full bg-slate-100 dark:bg-slate-900 group-hover:bg-white dark:group-hover:bg-pink-500/20 transition-colors">
                        <Upload size={20} className="text-slate-400 group-hover:text-pink-500" />
                    </div>
                    <span className="text-xs font-medium text-slate-500 group-hover:text-pink-500">Upload Image</span>
                </div>
            )}
            <input 
                type="file" 
                ref={fileInputRef} 
                onChange={handleFileChange} 
                className="hidden" 
                accept="image/*"
            />
        </div>
        <div className="absolute -right-1 top-1/2 -translate-y-1/2 w-4 h-8 flex items-center justify-center">
             <Handle type="source" position={Position.Right} className="!w-3 !h-3 !bg-pink-500 border-2 border-white dark:border-slate-900" />
        </div>
    </div>
  );
};

export default memo(ImageNode);
