import React, { memo, useState, useCallback } from 'react';
import { Handle, Position, NodeProps, useReactFlow } from 'reactflow';
import useStore from '@/store/useStore';
import { LLMNodeData, GeminiModel, TextNodeData, ImageNodeData } from '@/types/workflow';
import { Play, Loader2, Sparkles, MessageSquare, Bot } from 'lucide-react';
import clsx from 'clsx';

const LLMNode = ({ id, data, selected }: NodeProps<LLMNodeData>) => {
  const updateNodeData = useStore((state) => state.updateNodeData);
  const { getNodes, getEdges } = useReactFlow();
  const [isRunning, setIsRunning] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const performRun = useCallback(async () => {
    setIsRunning(true);
    setError(null);
    updateNodeData(id, { output: undefined, isLoading: true, error: undefined });

    try {
      const allNodes = getNodes();
      const allEdges = getEdges();
      
      const targetEdges = allEdges.filter((e) => e.target === id);
      
      let systemPrompt = "";
      let userMessage = "";
      const images: string[] = [];

      targetEdges.forEach((edge) => {
        const sourceNode = allNodes.find((n) => n.id === edge.source);
        if (!sourceNode) return;

        if (edge.targetHandle === 'system' && sourceNode.type === 'textNode') {
           systemPrompt += (sourceNode.data as TextNodeData).text + "\n";
        }
        if (edge.targetHandle === 'user' ) {
            if (sourceNode.type === 'textNode') userMessage += (sourceNode.data as TextNodeData).text + "\n";
            if (sourceNode.type === 'llmNode') userMessage += (sourceNode.data as LLMNodeData).output + "\n"; 
        }
        if (edge.targetHandle === 'images' && sourceNode.type === 'imageNode') {
            const imgData = (sourceNode.data as ImageNodeData).imageData;
            if (imgData) images.push(imgData);
        }
      });
      
      if (!userMessage.trim() && !images.length) {
          throw new Error("Input required: Connect detailed instructions or images.");
      }

      const response = await fetch('/api/run-llm', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
              model: data.model || 'gemini-1.5-flash',
              systemPrompt,
              userMessage,
              images
          })
      });

      if (!response.ok) {
          const errData = await response.json();
          throw new Error(errData.error || "Failed to run LLM");
      }

      const result = await response.json();
      const outputText = result.text;
      updateNodeData(id, { output: outputText, isLoading: false });

      const outputEdges = allEdges.filter(e => e.source === id);
      outputEdges.forEach(edge => {
          updateNodeData(edge.target, { text: outputText });
      });

    } catch (err: any) {
      console.error(err);
      setError(err.message);
      updateNodeData(id, { error: err.message, isLoading: false });
    } finally {
      setIsRunning(false);
    }
  }, [id, data.model, getNodes, getEdges, updateNodeData]);

  return (
    <div className={clsx(
        "shadow-xl rounded-2xl bg-white dark:bg-[#0f172a] min-w-[340px] transition-all duration-300 group",
        selected ? 'ring-2 ring-primary border-transparent' : 'border border-slate-200 dark:border-slate-800'
    )}>
        {/* Header */}
        <div className="px-4 py-3 border-b border-slate-100 dark:border-slate-800 flex items-center justify-between">
            <div className="flex items-center gap-2">
                <div className="p-1.5 rounded-lg bg-violet-500/10 text-violet-500">
                    <Sparkles size={14} className="fill-current" />
                </div>
                <span className="font-semibold text-slate-800 dark:text-slate-100 text-sm">Gemini Pro</span>
            </div>
            
             <select 
                className="bg-slate-100 dark:bg-slate-900 border border-slate-200 dark:border-slate-800 text-[10px] font-medium rounded-md px-2 py-1 outline-none focus:border-primary text-slate-600 dark:text-slate-400"
                value={data.model}
                onChange={(e) => updateNodeData(id, { model: e.target.value as GeminiModel })}
            >
                <option value="gemini-2.0-flash">Flash 2.0</option>
                <option value="gemini-1.5-pro">Pro 1.5</option>
            </select>
        </div>
        
        <div className="p-1">
             {/* Handles Section */}
             <div className="flex flex-col gap-1 py-2 px-3">
                <div className="relative flex items-center h-7">
                    <Handle type="target" position={Position.Left} id="system" className="!w-2 !h-2 !bg-slate-400" />
                    <span className="text-[10px] uppercase font-bold text-slate-400 ml-2">System</span>
                </div>
                <div className="relative flex items-center h-7 bg-slate-50 dark:bg-slate-900/50 rounded-md">
                    <Handle type="target" position={Position.Left} id="user" className="!w-2 !h-2 !bg-blue-500 !-ml-1" />
                    <span className="text-xs font-medium text-slate-600 dark:text-slate-300 ml-3 flex items-center gap-1">
                        <MessageSquare size={12} />
                        Prompt
                    </span>
                </div>
                <div className="relative flex items-center h-7">
                    <Handle type="target" position={Position.Left} id="images" className="!w-2 !h-2 !bg-pink-500" />
                    <span className="text-[10px] uppercase font-bold text-slate-400 ml-2">Images</span>
                </div>
             </div>

            {/* Content Area */}
            <div className="px-3 pb-3">
                <button
                    onClick={performRun}
                    disabled={isRunning}
                    className={clsx(
                        "w-full flex items-center justify-center gap-2 py-2 rounded-xl text-xs font-semibold uppercase tracking-wide transition-all mt-2",
                        isRunning 
                            ? "bg-slate-100 dark:bg-slate-800 text-slate-400" 
                            : "bg-primary text-white hover:bg-primary/90 shadow-lg shadow-primary/20 hover:shadow-primary/30"
                    )}
                >
                    {isRunning ? <Loader2 className="animate-spin" size={14} /> : <Play size={14} fill="currentColor" />}
                    {isRunning ? "Running..." : "Run"}
                </button>

                {error && (
                    <div className="mt-3 text-xs text-red-500 bg-red-500/10 p-2 rounded-lg border border-red-500/20">
                        {error}
                    </div>
                )}

                {data.output && (
                    <div className="mt-3 bg-slate-950 rounded-lg p-3 border border-slate-800 group/output relative">
                         <div className="text-[10px] text-slate-500 mb-2 uppercase tracking-wider font-semibold flex items-center justify-between">
                            Output
                            <Bot size={12} />
                        </div>
                        <div className="text-sm text-slate-300 leading-relaxed max-h-40 overflow-y-auto custom-scrollbar font-mono">
                            {data.output}
                        </div>
                    </div>
                )}
            </div>
        </div>
        
        {/* Output Handle */}
        <div className="absolute -right-1 top-1/2 -translate-y-1/2 w-4 h-8 flex items-center justify-center">
            <Handle type="source" position={Position.Right} className="!w-3 !h-3 !bg-primary border-2 border-white dark:border-slate-900" />
        </div>
    </div>
  );
};

export default memo(LLMNode);
