import React, { memo } from 'react';
import { Handle, Position, NodeProps } from 'reactflow';
import useStore from '@/store/useStore';
import { TextNodeData } from '@/types/workflow';
import { Type, AlignLeft } from 'lucide-react';
import clsx from 'clsx';

const TextNode = ({ id, data, selected }: NodeProps<TextNodeData>) => {
  const updateNodeData = useStore((state) => state.updateNodeData);

  const handleChange = (evt: React.ChangeEvent<HTMLTextAreaElement>) => {
    updateNodeData(id, { text: evt.target.value });
  };

  return (
    <div className={clsx(
        "shadow-xl rounded-2xl bg-white dark:bg-[#0f172a] min-w-[280px] transition-all duration-300",
        selected ? 'ring-2 ring-blue-500 border-transparent' : 'border border-slate-200 dark:border-slate-800'
    )}>
      <div className="px-4 py-3 border-b border-slate-100 dark:border-slate-800 flex items-center justify-between">
            <div className="flex items-center gap-2">
                <div className="p-1.5 rounded-lg bg-blue-500/10 text-blue-500">
                    <Type size={14} className="fill-current" />
                </div>
                <span className="font-semibold text-slate-800 dark:text-slate-100 text-sm">Text Input</span>
            </div>
      </div>
      
      <div className="p-3">
        <div className="relative group">
            <textarea
            className="nodrag w-full text-xs p-3 rounded-xl border border-slate-200 dark:border-slate-800 bg-slate-50 dark:bg-slate-900/50 text-slate-900 dark:text-slate-200 focus:outline-none focus:ring-2 focus:ring-blue-500/20 focus:border-blue-500/50 h-32 resize-y font-mono transition-all leading-relaxed custom-scrollbar"
            placeholder="Type something..."
            value={data.text}
            onChange={handleChange}
            />
            <div className="absolute top-2 right-2 p-1 rounded hover:bg-slate-200 dark:hover:bg-slate-800 text-slate-400 dark:text-slate-600 transition-colors pointer-events-none">
                <AlignLeft size={12} />
            </div>
        </div>
      </div>

      <div className="absolute -left-1 top-1/2 -translate-y-1/2 w-4 h-8 flex items-center justify-center">
        <Handle type="target" position={Position.Left} className="!w-3 !h-3 !bg-slate-400 border-2 border-white dark:border-slate-900" />
      </div>
      <div className="absolute -right-1 top-1/2 -translate-y-1/2 w-4 h-8 flex items-center justify-center">
        <Handle type="source" position={Position.Right} className="!w-3 !h-3 !bg-blue-500 border-2 border-white dark:border-slate-900" />
      </div>
    </div>
  );
};

export default memo(TextNode);
