import React, { useState } from 'react';
import { 
    Type, Image as ImageIcon, Bot, 
    LayoutGrid, Search, Layers, Command, 
    Settings, FolderOpen, Puzzle 
} from 'lucide-react';
import useStore from '@/store/useStore';

const Sidebar = () => {
  const [activeTab, setActiveTab] = useState<'nodes' | 'layers' | 'files'>('nodes');
  
  const onDragStart = (event: React.DragEvent, nodeType: string) => {
    event.dataTransfer.setData('application/reactflow', nodeType);
    event.dataTransfer.effectAllowed = 'move';
  };

  return (
    <div className="flex h-full z-10">
      {/* Navigation Rail */}
      <div className="w-14 flex flex-col items-center py-4 bg-slate-50 dark:bg-[#020617] border-r border-slate-200 dark:border-slate-800 gap-4">
        
        <NavButton active={activeTab === 'nodes'} onClick={() => setActiveTab('nodes')} icon={<Puzzle size={20} />} tooltip="Nodes" />
        <NavButton active={activeTab === 'layers'} onClick={() => setActiveTab('layers')} icon={<Layers size={20} />} tooltip="Layers" />
        <NavButton active={activeTab === 'files'} onClick={() => setActiveTab('files')} icon={<FolderOpen size={20} />} tooltip="Files" />
        
        <div className="flex-1" />
        
        <NavButton active={false} onClick={() => {}} icon={<Settings size={20} />} tooltip="Settings" />
      </div>

      {/* Side Panel */}
      <div className="w-64 bg-slate-50 dark:bg-[#020617] border-r border-slate-200 dark:border-slate-800 flex flex-col transition-all duration-300">
        
        {/* Panel Header */}
        <div className="h-14 border-b border-slate-200 dark:border-slate-800 flex items-center px-4 font-semibold text-sm">
            {activeTab === 'nodes' && "Components"}
            {activeTab === 'layers' && "Layers"}
            {activeTab === 'files' && "Project Files"}
        </div>

        {/* Search */}
        <div className="p-4 pb-0">
            <div className="relative group">
                <Search size={14} className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400 group-focus-within:text-primary transition-colors" />
                <input 
                    type="text" 
                    placeholder="Search..." 
                    className="w-full bg-slate-200/50 dark:bg-slate-900 border border-transparent focus:border-primary/50 text-sm rounded-lg pl-9 pr-3 py-2 outline-none focus:ring-2 focus:ring-primary/10 transition-all"
                />
            </div>
        </div>

        {/* Content */}
        <div className="flex-1 overflow-y-auto p-4 space-y-6">
            
            {activeTab === 'nodes' && (
                <>
                    <div className="space-y-3">
                        <SectionHeader title="Logic" />
                        <DraggableItem 
                            type="llmNode" 
                            label="Gemini Pro" 
                            desc="Multimodal AI Model" 
                            icon={<Bot size={16} />} 
                            color="text-violet-500"
                            bg="bg-violet-500/10"
                            onDragStart={onDragStart}
                        />
                    </div>
                    
                    <div className="space-y-3">
                        <SectionHeader title="Input / Output" />
                        <DraggableItem 
                            type="textNode" 
                            label="Text Input" 
                            desc="Prompt or Content" 
                            icon={<Type size={16} />} 
                            color="text-blue-500"
                            bg="bg-blue-500/10"
                            onDragStart={onDragStart}
                        />
                         <DraggableItem 
                            type="imageNode" 
                            label="Image Input" 
                            desc="Upload or Link" 
                            icon={<ImageIcon size={16} />} 
                            color="text-pink-500"
                            bg="bg-pink-500/10"
                            onDragStart={onDragStart}
                        />
                    </div>
                </>
            )}

            {activeTab !== 'nodes' && (
                <div className="flex flex-col items-center justify-center h-40 text-slate-400 text-xs text-center p-4">
                    <span className="mb-2 opacity-50"><Command size={24} /></span>
                    Functionality coming soon
                </div>
            )}

        </div>
      </div>
    </div>
  );
};

const NavButton = ({ active, onClick, icon, tooltip }: { active: boolean, onClick: () => void, icon: React.ReactNode, tooltip: string }) => (
    <button 
        onClick={onClick}
        title={tooltip}
        className={`w-9 h-9 flex items-center justify-center rounded-lg transition-all duration-200 group relative
        ${active 
            ? 'bg-primary text-white shadow-lg shadow-primary/25' 
            : 'text-slate-400 hover:bg-slate-100 dark:hover:bg-slate-800 hover:text-slate-600 dark:hover:text-slate-200'
        }`}
    >
        {icon}
        {/* Tooltip could go here */}
    </button>
);

const SectionHeader = ({ title }: { title: string }) => (
    <div className="text-[10px] font-bold text-slate-400 uppercase tracking-widest pl-1">{title}</div>
);

const DraggableItem = ({ type, label, desc, icon, color, bg, onDragStart }: any) => (
    <div 
        draggable 
        onDragStart={(e) => onDragStart(e, type)}
        className="flex items-center gap-3 p-3 rounded-xl border border-slate-200 dark:border-slate-800 bg-white dark:bg-[#0f172a] hover:border-primary/50 dark:hover:border-primary/50 hover:shadow-md cursor-grab active:cursor-grabbing transition-all group select-none"
    >
        <div className={`w-10 h-10 rounded-lg ${bg} ${color} flex items-center justify-center shrink-0`}>
            {icon}
        </div>
        <div className="flex-1 overflow-hidden">
            <div className="text-sm font-semibold text-slate-700 dark:text-slate-200 truncate">{label}</div>
            <div className="text-[10px] text-slate-500 truncate">{desc}</div>
        </div>
    </div>
);

export default Sidebar;
